import React from "react";
import { BrowserRouter as Router, Link, Switch, Route } from "react-router-dom";
import styles from "./css/ReactRouter.module.css";
import "bootstrap/dist/css/bootstrap.min.css";
import Nav from "react-bootstrap/Nav";
import Navbar from "react-bootstrap/Navbar";
import Container from "react-bootstrap/Container";
import Home from "./Home";
import AboutUs from "./AboutUs";
import ContactUs from "./ContactUs";
import { GetUsers, GetUsersById } from "./GetUsers";
function Header() {
  return (
    <Navbar
      collapseOnSelect
      expand="lg"
      fixed="top"
      className={`p-4 mb-5 ${styles.Navbar}`}
    >
      <Navbar.Brand className={`${styles.Brand} font-weight-bolder text-light`}>
        <h1>React-Routing</h1>
      </Navbar.Brand>
      <Navbar.Toggle
        aria-controls="responsive-navbar-nav"
        className="bg-light"
      />
      <Navbar.Collapse id="responsive-navbar-nav">
        <Nav className="ml-auto font-weight-bold">
          <Link to="/" className={`${styles.Link} m-3`}>
            Home
          </Link>
          <Link to="/about-us" className={`${styles.Link} m-3`}>
            About Us
          </Link>
          <Link to="/contact-us" className={`${styles.Link} m-3`}>
            Contact Us
          </Link>
          <Link to="/get-info" className={`${styles.Link} m-3`}>
            Get Info
          </Link>
          <Link to="/get-info-by-id" className={`${styles.Link} m-3`}>
            Get Info(id)
          </Link>
        </Nav>
      </Navbar.Collapse>
    </Navbar>
  );
}

function Footer() {
  return (
    <>
      <div className={`${styles.Footer} p-4`}>
        <p className="my-auto">
          Author: Kajal Karmakar
          <br />
          <a href="mailto:kajal.karmakar@theonetechnologies.co.in">
            kajal.karmakar@theonetechnologies.co.in
          </a>
        </p>
      </div>
      <p className={`${styles.Copy} ${styles.Footer} mb-0`}>
        <small>Copyright &copy; 2020</small>
      </p>
    </>
  );
}
function ReactRouter() {
  return (
    <Router>
      <div>
        <Header />
        <br />
        <Container className={`mt-5 pt-5 ${styles.DisplayDetails}`}>
          <Switch>
            <Route exact path="/">
              <Home />
            </Route>
            <Route path="/about-us">
              <AboutUs />
            </Route>
            <Route path="/contact-us">
              <ContactUs />
            </Route>
            <Route path="/get-info">
              <GetUsers />
            </Route>
            <Route path="/get-info-by-id/:Id?">
              <GetUsersById />
            </Route>
          </Switch>
        </Container>
        <Footer />
      </div>
    </Router>
  );
}
export default ReactRouter;
