import React from "react";
import styles from "./css/ReactRouter.module.css";
import "bootstrap/dist/css/bootstrap.min.css";
import Container from "react-bootstrap/Container";
import { useParams } from "react-router-dom";

function GetUsersList(props) {
  return (
    <React.Fragment>
      <Container className={`mx-auto my-5 px-5`}>
        <h1 className={`${styles.Title} mb-3`}>{props.user.title}</h1>
        <h5>Name: {props.user.Name}</h5>
        <h5>Age: {props.user.Age}</h5>
        <h5>DOB: {props.user.DOB}</h5>
        <h5>City: {props.user.City}</h5>
        <h5>State: {props.user.State}</h5>
        <h5>Country: {props.user.Country}</h5>
      </Container>
    </React.Fragment>
  );
}
const userDetails = [
  {
    id: 1,
    title: "User 1",
    Name: "Kajal",
    Age: 21,
    DOB: "14 - 03 - 1998",
    City: "Vapi",
    State: "Gujarat",
    Country: "India",
  },
  {
    id: 2,
    title: "User 2",
    Name: "Amit",
    Age: 29,
    DOB: "14 - 03 - 1990",
    City: "Vapi",
    State: "Gujarat",
    Country: "India",
  },
  {
    id: 3,
    title: "User 3",
    Name: "Abhishek",
    Age: 22,
    DOB: "07 - 04 - 1997",
    City: "Vadodara",
    State: "Gujarat",
    Country: "India",
  },
  {
    id: 4,
    title: "User 4",
    Name: "Pooja",
    Age: 25,
    DOB: "18 - 12 - 1994",
    City: "Junagadh",
    State: "Gujarat",
    Country: "India",
  },
];

function GetUsers() {
  return (
    <React.Fragment>
      {userDetails.map((user) => (
        <GetUsersList user={user} key={user.id} />
      ))}
    </React.Fragment>
  );
}
function GetUsersById() {
  const { Id } = useParams();
  return (
    <React.Fragment>
      <div className="pt-5">
        {Id === undefined ? (
          <h1 className="text-danger">You should pass user id as parameter</h1>
        ) : (
          <h1>Displaying user info with ID {Id}</h1>
        )}{" "}
      </div>
      {userDetails
        .filter((user) => JSON.stringify(user.id) === Id)
        .map((users) => (
          <GetUsersList user={users} key={users.id} />
        ))}
    </React.Fragment>
  );
}
export { GetUsers, GetUsersById };
